import React, { useContext, useEffect, useRef, useState } from "react";
import { sCon } from "../pages/sCon";
import $ from "jquery";

//data
// props    :  ,    
// import { options, product } from "../data/product";

//module
import { addComma } from "../func/common_fn";
//css
import "../../css/cart_list.scss";
/////import area////////////////////////////

function OptionTable({
  selData,
  optData,
  selectedOpt,
  setSelectedOpt,
  selOptData,
  setSelOptData,
}) {
  //OptionTable  : potionBox    (selOptData,
  //setSelOptData)    +    
  // ShopCustomCont     localCart  

  // props list
  //1. selData :   (  )
  //2. optData : 
  //3. selectedOpt: ,    
  //4. setSelectedOpt : ,      
  //5. selOptData, setSelOptData :    

  // console.log(" selOptData:", selOptData);
  // console.log(" setSelectedOpt ?:", selectedOpt.value);
  // console.log(" ", selData);

  // 
  //1.   
  const priceRef = useRef([]);
  //2.     
  const optRef = useRef([]);
  // console.log("  ?2", priceRef.current);


  //  ()//////////////////////////////////
  useEffect(() => {
    //1. 

    //1) 
    const sum = $(".sum");
    //2)   //   ..
    const numBtn = $(".chg_num img");
    console.log("  ?", numBtn);
    console.log("  ?", numBtn.length);
    //3) 
    const total = $("#total");

    //4) price     each   priceNum   
    $(".price-num").each((i, price) => {
      //(1)   
      const priceText = $(price).text();
      console.log(" 2", priceText);

      //(2) priceText  
      const priceNum = parseInt(priceText.replace(/[^0-9]/g, ""), 10); //    

      //(3)   priceNum  priceRef 
      priceRef.current[i] = priceNum;

    }); // each
    console.log("  ?3", priceRef.current);
    const refNum = priceRef.current;

    //2.   
    const numBtnFn = ()=>{
      numBtn.off("click").on("click", (e) => {
        //1)    (0  / 1 )
        let seq = $(e.currentTarget).index();
        let pseq = $(e.currentTarget).parents("tr").index();
        console.log(" ?", seq, e.currentTarget,pseq);
  
        //2-1.      total  
  
        optRef.current[pseq] = Number(sum.eq(pseq).val());
        $(e.currentTarget).parent().prev("input").val(seq == 0 ? ++optRef.current[pseq] : optRef.current[pseq] == 1 ? 1 : --optRef.current[pseq]);
        console.log(" ", optRef.current[pseq]);
  
        console.log("refNum  :", refNum);
        //5) priceNum     resNum  
        const resPrice = refNum.reduce((acc, cur) => acc + cur * optRef.current[pseq], 0);
        total.text(addComma(resPrice));
        console.log("total?", total);
      }); //click

    };
    numBtnFn();
  });



  ///////////////////////////////////////////////
  return (
    <>
      <table>
        {/*  */}
        <thead style={{ display: "none" }}>
          <tr>
            <th scope="col"></th>
            <th scope="col"></th>
            <th scope="col"></th>
            <th scope="col"></th>
          </tr>
        </thead>
        <tbody>
          {/*    */}
          <tr className="default-option">
            {/* 1.   */}
            <td>{selData.tit}</td>
            {/* 2.   */}
            <td className="cnt-part">
              <span>
                <input type="text" className="sum" defaultValue="1" />
                <b className="chg_num">
                  <img
                    src={process.env.PUBLIC_URL + "/images/cnt_up.png"}
                    alt=""
                  />
                  <img
                    src={process.env.PUBLIC_URL + "/images/cnt_down.png"}
                    alt=""
                  />
                </b>
              </span>
            </td>
            {/* 3.   */}
            <td>
              <span
                className="price-num"
                ref={(ele) => (priceRef.current[0] = ele)}
              >
                {addComma(selData.price)}
              </span>
              
            </td>
          </tr>
          {/* {value: " ", price: "2000", IsSoldOut: false} */}
          {selOptData.length > 0 &&
            selOptData.map((v, i) => (
              <tr className="add-option" key={i}>
                {/* 1.   */}
                <td>{v.value}</td>
                {/* 2.   */}
                <td className="cnt-part">
                  <span>
                    <input type="text" className="sum" defaultValue="1" />
                    <b className="chg_num">
                      <img
                        src={process.env.PUBLIC_URL + "/images/cnt_up.png"}
                        alt=""
                      />
                      <img
                        src={process.env.PUBLIC_URL + "/images/cnt_down.png"}
                        alt=""
                      />
                    </b>
                  </span>
                </td>
                {/* 3.   */}
                <td>
                  <span
                    className="price-num"
                    ref={(ele) => (priceRef.current[i + 1] = ele)}
                  >
                    {addComma(v.price)}
                  </span>
                  
                </td>
              </tr>
            ))}
        </tbody>
        {/*   */}
      </table>
      {/*   */}
      <div
        
        className="total-price"
        
      >
        <span id="total">{addComma(selData.price)}</span>
      </div>
    </>
  );
}

export default OptionTable;
