// pj -   ///
import { Link } from "react-router-dom";
import { useContext, useEffect, useState } from "react";
import $ from "jquery";

//css
import "../../css/login.scss";
//    !
import { initData } from "../func/memFn";
import { sCon } from "./sCon";

/////import area///////////////////////////////////////////////

export default function Login() {
    //  
    const myCon = useContext(sCon);

    

    //[]/////////////////////////////////////
    // [1]  
    // 1. 
    const [userId, setUserId] = useState("");
    // 2. 
    const [pwd, setPwd] = useState("");

    // [2]  
    // ->   (false)
    // 1. 
    const [userIdError, setUserIdError] = useState(false);
    // 2. 
    const [pwdError, setPwdError] = useState(false);

    //[3]    
    const [chkSaveId, setChkSaveId] = useState(false);
    ////////////////////////////////////////////////////////////////

    //[ ]/////////////
    //
    const msgId = [
        " ", //
        "  ",
    ];
    //
    const msgPwd = [
        " ",
        "  (,,  16 )",
    ];

    //[3] :  msgId[0] >>  
    const [idMsg, setIdMsg] = useState(msgId[0]);
    const [pwdMsg, setPwdMsg] = useState(msgPwd[0]);

    //[ ]//////////////////////////////////
    //1.   
    const changeUserId = (e) => {
        //0.   
        let val = e.target.value;

        //1.   (X = false:)
        if (val !== "") setUserIdError(false);
        // 
        else {
            //1) ()
            setIdMsg(msgId[0]);
            //2)  
            setUserIdError(true);
        } /////else

        setUserId(val);
    }; /////changeUserId//////////////////////////////////////////

    //[2.   ]
    const changePwd = (e) => {
        //0.   
        let val = e.target.value;

        //1.   
        // const valid =
            // /^.*(?=^.{5,16}$)(?=.*\d)(?=.*[a-zA-Z])(?=.*[!@#$%^&+=]).*$/;
        // : .test()

        //1.   (X = false:)
        if (val !== "") setPwdError(false);
        else {
            //1)  ()
            setPwdMsg(msgPwd[0]);
            //2) 
            setPwdError(true);
        }

        //   
        // if (valid.test(val)) setPwdError(false);
        // //  
        // else {
        //     //1)  
        //     setPwdMsg(msgPwd[1]);

        //     //2)  
        //     setPwdError(true);
        // }

        //   setUserId  
        setPwd(val);
    }; /////changePwd//////////////////////////////////////////

    //[  ]///////
    const totalValid = () => {
        // 1.     !
        if (!userId) setUserIdError(true);
        if (!pwd) setPwdError(true);

        // 2.  true,  false 
        //  :  +   false
        if (userId && pwd && !userIdError && !pwdError) return true;
        //  false false !
        else return false;
    }; //totalValid Fn////////////

    //[ ]//////////////////////
    const onSubmit = (e) => {
        //1.  
        e.preventDefault();
        console.log(":", totalValid());

        if (totalValid()) {
            //[  ]
            initData();
            //2.  
            let memData = localStorage.getItem("mem-data");
            //3.   
            memData = JSON.parse(memData);
            //4.   
            let result = memData.find((v) => {
                // vid userId  true 
                if (v.uid === userId) return true;
            }); ///find/////////////

            //4-1  x =  
            if (!result) {
                //1)   /   
                setIdMsg(msgId[1]);

                //2)  true
                setUserIdError(true);
            } ////if///////////////////////////////
            //4-2.    
            else {
                //1)   
                setUserIdError(false);
                //2)   :  == 
                if (pwd === result.pwd) {
                    //   !
                    alert("  ");

                    // ****** [    ] ****** //
                    // 1.    !
                    // ->    !
                    // ->  result  
                    // ->  JSON.stringify()
                    sessionStorage.setItem("login-sts", JSON.stringify(result));
                    // >      minfo 

                    //2.  API  
                    myCon.setLoginSts(
                        sessionStorage.getItem("login-sts")
                        //sessionStorage    
                        // >      
                    );

                    //5.   
                    //1    
                    setTimeout(() => {
                        myCon.goPage("/");
                    }, 1000);
                } //if
                // 
                else {
                    //1)   
                    setPwdMsg(msgPwd[1]);
                    setPwdError(true);
                }
            } ///else////////////////////////////

            //find() >  !!  filter
        } //////if

        //   
        else {
            alert("   ");
        }
    };

    // const enterkey = onSubmit();

    ///  ///////////////////////////////////
    useEffect(() => {
        //  
        $("#mid").focus();

        //      
        //  
        const savedUserId = localStorage.getItem("savedUserId");
        if (savedUserId) {
            setChkSaveId(true);
            setUserId(savedUserId);
        }
    }, []);
    ///  ///////////////////////////////////
    useEffect(() => {
        if (chkSaveId) {
            localStorage.setItem("savedUserId", userId);
        } else {
            localStorage.removeItem("savedUserId");
        }
    }, [chkSaveId, userId]);

    ////   //////////////
    return (
        <>
            <main id="login-area">
                {/* 1.   */}
                <section>
                    <div className="title">
                        <h3> Seulmit</h3>
                        <h3>   </h3>
                    </div>
                    <form action="">
                    {/* input-wrap */}
                    <div className="input-wrap">
                        {/*2.   */}
                        <div className="minput">
                            <input
                                className="input-box"
                                id="mid"
                                name="mid"
                                type="text"
                                maxLength="16"
                                placeholder="() "
                                value={userId}
                                onChange={changeUserId}
                                // onKeyUp={onSubmit}
                            ></input>
                            {
                                //   
                                userIdError && (
                                    <div
                                        className="msg"
                                        style={{ padding: "2px" }}
                                    >
                                        <small
                                            style={{
                                                textAlign: "left",
                                                color: "yellow",
                                                fontSize: "13px",
                                            }}
                                        >
                                            {idMsg}
                                        </small>
                                    </div>
                                )
                            }
                        </div>

                        {/*3.   */}
                        <div className="minput">
                            <input
                                className="input-box"
                                id="mpw"
                                name="mpw"
                                type="password"
                                maxLength="16"
                                placeholder=" (6~12 )"
                                value={pwd}
                                onChange={changePwd}
                                // onKeyUp={onSubmit}
                            ></input>
                            {
                                //   
                                pwdError && (
                                    <div
                                        className="msg"
                                        style={{ padding: "2px" }}
                                    >
                                        <small
                                            style={{
                                                textAlign: "left",
                                                color: "yellow",
                                                fontSize: "13px",
                                            }}
                                        >
                                            {pwdMsg}
                                        </small>
                                    </div>
                                )
                            }
                        </div>
                        {/* 4.   */}
                        <div className="saveId">
                            <input
                                type="checkbox"
                                id="saveId"
                                name="saveId"
                                onChange={(e) => {
                                    setChkSaveId(e.target.checked);
                                    // console.log("",setChkSaveId)
                                }}
                                checked={chkSaveId}
                            />
                            <label htmlFor="saveId"> </label>
                        </div>
                        {/* input-wrap end */}
                    </div>

                    {/* btn-wrap-start */}
                    <div className="btn-wrap">
                        <button className="button-nomal2" 
                        onClick={onSubmit}
                        onKeyUp={onSubmit}
                        >
                            LOG IN
                        </button>
                        <div className="desc-wrap fx-box">
                            <span>/ ?</span>
                            <div className="fx-box">
                                <a href=""> </a>
                                <a href=""> </a>
                            </div>
                        </div>
                        <div className="desc">
                            <span> Seulmit ?</span>
                        </div>
                        <Link to="/member">
                            <button className="button-fill">SING UP</button>
                        </Link>
                    </div>
                    {/* btn-wrap-end */}
                    </form>
                </section>
            </main>
        </>
    );
} /////////// Login /////////////////////
