import React, { useRef } from "react";
import { useState, useEffect } from "react";
import "../../css/act_text.scss"; // CSS  
import $ from "jquery";


  function Act_text({ text }) {

    //text :   
    const [isAni, setIsAni] = useState(false);
    const [displayedChars, setDisplayedChars] = useState([]);

    const charsRef = useRef(text.replace(/&nbsp;/g, ' ').split('')); // chars useRef 
    const retVal = x => x.getBoundingClientRect().top;

    useEffect(() => {

      $(window).on("scroll", function () {
        const windowHeight = $(this).height();
        const triggerPoint = windowHeight / 2;

document.querySelectorAll(".act-text").forEach(v=>{
  if(retVal(v) < triggerPoint){
    v.classList.add("on");
  }
});
  
        // if (scrollTop > triggerPoint && !isAni) {
        //   setIsAni(true); //      isAni true 
        // }
      });
    }, []);


    useEffect(() => {
      let index = 0;
      let intervalId; // intervalId  
  
      const chars = text.replace(/&nbsp;/g, ' ').split(''); // 
        // console.log("zzz",chars);


      // intervalId = setInterval(() => {
        if (index < chars.length) {
          setDisplayedChars(chars);
          // index++;
          // setIsAni(false);
        } 
      // },0); // 200ms    
  
      return () => clearInterval(intervalId); //    setInterval 
    }, [isAni]); // text   
  
    return (
      <div className="act-text">
        {displayedChars.map((char, i) => (
          <span
            key={i}
            className="seulmit-text"
            style={{ transitionDelay: `${i * 0.1}s` }}
          >
            {char}
          </span>
        ))}
      </div>
    );
  }
  
  export default Act_text;