import React, { useEffect } from "react";
import { product } from "../data/product";
import { Link, useLocation, useNavigate } from "react-router-dom";
import $ from "jquery";

import "../../css/shop.scss";
import { addComma } from "../func/common_fn";

////import area//////////////////////////////

function ShopPdList({ products }) {
  //products: products={product[selCat]} :   

  // console.log(" ",selData);

  //  li key 
  //Object.keys(products) 
  //     key 
  //      
  // selData .face  Object.keys(products);   

  // selData  
  //products    

  const nav = useNavigate();
  const clickProduct = (product) => {
    nav("/detail", { state: { product: product.idx, topcat: $(".cgmenu a.on").text() }, replace: true });
  };


  // 
  // const clickProduct = (product) => {
  //   console.log(product);
  //   nav("/detail", { state: { product:product.idx, topcat:$(".cgmenu a.on").text() } });
  //   //  state  
  // };

  return (
    <>
      <div className="pd-list">
        {products.map((v, i) => (
          //product  
          <li
            key={i}
            className="item"
            onClick={() => {
              clickProduct(v);
            }}
          >
            <Link
              to={`/Detail/${v.idx}`}
              // onClick={(e) => {
              //   e.preventDefault();
              //   clickProduct(v);
              // }}
            >
              <div className="img-wrap">
                <div className="imgbx">
                  <img src={process.env.PUBLIC_URL+v.img[0].isrc} alt={v.tit} />
                </div>
                <div className="blur">
                  <img src={process.env.PUBLIC_URL+v.img[1].isrc} alt={v.tit} />
                </div>
              </div>
              <div className="desc-wrap">
                <div className="eng-pdtit">{v.engtit}</div>
                <div className="kor-pdtit">{v.tit}</div>
                <div className="price">
                  {addComma(v.price)} <span></span>
                </div>
              </div>
            </Link>
          </li>
        ))}
      </div>
    </>
  );
}

export default ShopPdList;
