// 
import React, { useContext, useEffect, useRef, useState } from "react";
import { Link, useNavigate } from "react-router-dom";
import $ from "jquery";
import { sCon } from "../pages/sCon";

//  
import { addComma } from "../func/common_fn";

//css
import "../../css/cart_list.scss";
//////import area/////////////////////////////////////////

function CartList() {
  // 
  const myCon = useContext(sCon);
  // const { selCat } = useContext(sCon);
  //   
  const [force, setForce] = useState(false);

  //  
  const [selData, setSelData] = useState([]);

  //  
  // const selData = JSON.parse(myCon.localsCart || "[]");
  console.log("", selData);

  //  
  const dataCnt = selData.length;

  //  ///////////////
  const totalFn = () => {
    //     
    // each 

    let result = 0;
    $(".snm-num2").each((idx, ele) => {
      result += Number($(ele).val());
    });
    //   
    return result;
  }; //////totalFn

  //   : dataCnt,  !   (force) /////////
  useEffect(() => {
    $(".total-num").text(addComma(totalFn()));
  }, [dataCnt, force]);

  //   
  const tgRef = useRef(null);

  //  
  const BoxCloseFn = ({ tg, selData }) => {
    //tg :  
    //selData :   
    //
    $(".blocking-bg").fadeOut(200);
    $(tg.current).stop().animate(
      {
        right: "-72vw",
      },
      500
    );
  }; /////BoxCloseFn
  useEffect(() => {
    // console.log("tgRef:", tgRef);
  });

  //    
  useEffect(() => {
    try {
      setSelData(JSON.parse(myCon.localsCart || "[]")); //      
    } catch (error) {
      console.error("    :", error);
      setSelData([]); //      
    }
  }, [myCon.localsCart]);

  /////// /////////////////////////////
  return (
    <>
      <div className="cart-list" ref={tgRef}>
        {/*  */}
        <button
          className="close-btn"
          onClick={() => {
            BoxCloseFn({ tg: tgRef, selData });
          }}
        >
          <span className="icon-img material-symbols-outlined search-btn">
            close
          </span>
        </button>
        {/*   */}
        <table>
          <colgroup>
            <col span="1" style={{ width: "5%" }} />
            <col span="2" style={{ width: "20%" }} />
            <col span="2" style={{ width: "15%" }} />
            <col span="2" style={{ width: "20%" }} />
            <col span="2" style={{ width: "5%" }} />
            <col span="2" style={{ width: "5%" }} />
            <col span="3" style={{ width: "5%" }} />
          </colgroup>

          <caption>CART LIST <span>({dataCnt})</span></caption>
          {/*   */}
          <thead>
            <tr>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
            </tr>
          </thead>
          {/*   */}

          <tbody>
            {selData.map((v, i) => (
              <tr key={i}>
                <td>{i + 1}</td>
                {/*  */}
                {/*  */}
                <td className="img-bx">
                  <img
                    src={
                      process.env.PUBLIC_URL +
                      `/images/shop/${
                        v.category == "homefragrance" ? "home" : v.category
                      }/${
                        v.category == "homefragrance" ? "home" : v.category
                      }_${`${i % 2 === 1 ? 
                        (i < 9 ? `0${(i+1) + 1}` : (i+1) + 1) 
                        : ( i < 8 ? `0${i + 1}` : i + 1)}`}.jpg`

                      // }_${`${i + 1}`.padStart(2, "0")}.jpg`
                      // i  ?  10  "0" + idx+1 :  10  idx+1
                      // i  ?  10  "0" + idx+2 :  10  idx +2
                      // 
                    }
                    alt="item"
                  />
                </td>
                {/*  */}
                <td>{v.tit}</td>
                {/*  */}
                <td>{addComma(v.price)}</td>
                {/*  */}
                <td className="cnt-part">
                  <div>
                    {/*     */}
                    <span>
                      <input
                        type="text"
                        className="item-cnt"
                        readOnly
                        defaultValue={v.cnt}
                        onBlur={() => {}}
                      />
                      {/*  */}
                      <button
                        className="btn-insert"
                        onClick={(e) => {
                          /// 1.     
                          // : selData -> 
                          // i !(map  i )
                          selData[i].cnt = $(e.currentTarget)
                            .siblings(".item-cnt")
                            .val();
                          console.log(":", selData);
                          // 2.   :   
                          let res = JSON.stringify(selData);

                          // 3. "cart-data"
                          localStorage.setItem("cart-data", res);

                          // 4.   
                          myCon.setLocalsCart(res);

                          // 5.  
                          // $(e.currentTarget).css({width:"0"});
                          //->  6   !
                          //  sync    
                          //    :
                          //      
                          //   !
                          //    
                          setForce(!force);

                          //6.   
                          // $(".total-num").text(addComma(totalFn()));
                        }}
                      >
                        
                      </button>
                      <button
                        className="btn-cancel"
                        onClick={(e) => {
                          $(e.currentTarget)
                            // .css({width:"0"})
                            .hide()
                            .prev() //

                            // .css({width:"0"})
                            .hide()
                            .siblings("input")
                            .val(v.cnt);
                          //  css ()
                          //   ,  input 
                          // v.cnt !
                        }}
                      >
                        
                      </button>
                      <b
                        className="btn-cnt"
                        onClick={(e) => {
                          //  (input)
                          let tg = $(e.currentTarget).siblings("input");

                          //  blur  
                          //   !
                          // tg.focus();

                          //    
                          // e.target  
                          //   !
                          // e.currentTarget  
                          //  !(!)
                          let btnAlt = $(e.target).attr("alt");
                          console.log(btnAlt);
                          //   
                          if (btnAlt == "") {
                            // tg  1 !
                            tg.val(Number(tg.val()) + 1);
                          } //// if ///////
                          else if (btnAlt == "") {
                            // tg  1 !
                            // , 1   !
                            tg.val(
                              Number(tg.val()) == 1 ? 1 : Number(tg.val()) - 1
                            );
                          } ///// else if ////////

                          //   
                          $(e.currentTarget)
                            .siblings(".btn-insert")
                            .show()
                            .next()
                            .show();
                        }}
                      >
                        <img
                          src={process.env.PUBLIC_URL + "/images/cnt_up.png"}
                          alt=""
                        />
                        <img
                          src={process.env.PUBLIC_URL + "/images/cnt_down.png"}
                          alt=""
                        />
                      </b>
                    </span>
                  </div>
                </td>
                {/*  */}
                <td>
                  <span className="sum-num1">
                    {addComma(v.price * (v.cnt ? v.cnt : 1))}
                  </span>
                  
                  <input
                    className="sum-num2"
                    type="hidden"
                    defaultValue={v.price * (v.cnt ? v.cnt : 1)}
                  />
                </td>
                {/*  */}
                <td>
                  <button
                    className="cfn"
                    onClick={() => {
                      // confirm() "" true
                      if (
                        window.confirm(
                          " ?"
                        )
                      ) {
                        // console.log("!!!");
                        // console.log(":",selData);
                        // console.log(":",i);
                        // // splice     
                        // console.log(":",selData.splice(i,1));

                        //    map() i 
                        //   selData
                        // 1.  :
                        selData.splice(i, 1);

                        // 2.   :   
                        let res = JSON.stringify(selData);

                        // 3. "cart-data"
                        localStorage.setItem("cart-data", res);

                        // 4.   
                        myCon.setLocalsCart(res);

                        // 5.  0 
                        //  false 
                        //   !
                        if (selData.length == 0) myCon.setCartSts(false);
                        
                        // let aa = [];
                        // aa.splice(,)
                      } //// if /////
                    }}
                  >
                    
                  </button>
                </td>
              </tr>
            ))}
          </tbody>
          {/*   */}
          <tfoot>
            <tr>
              <td colSpan="6"> :</td>
              <td>
                <span className="total-num"></span> 
              </td>
            </tr>
            <tr>
              <td colSpan="7" className="paging">
                <button className="btn-buy button-nomal"> </button>
              </td>
            </tr>
          </tfoot>
        </table>
      </div>
      <div className="blocking-bg"></div>
    </>
  );
}

export default CartList;
