//////   
import React, { useEffect, useRef, useState } from "react";
import $ from "jquery";

// Import Swiper React components
import { Swiper, SwiperSlide } from "swiper/react";
// Import Swiper styles
import "swiper/css";
import "swiper/css/free-mode";
import "swiper/css/navigation";
import "swiper/css/thumbs";

import "./css/swiper_ThumbsList.scss";

// import required modules
import { FreeMode, Navigation, Thumbs } from "swiper/modules";
//data
import { product } from "../data/product";

//////////improt area//////////////////////////////////////

export default function SwiperThumbsList({ catName,seq,cnt }) {

  /* 
  []
  1. catName: product   key  //    
  2. seq :   (data : product > [key : idx   ] >     thumbCnt    )
  3. cnt : key
  4. num
  
  */


  // const selData = product[catName];
  const selDataCnt = cnt;//product[catName][seq].thumbCnt;
 
  const selData = Object.keys(product)[catName];
  // console.log(" : \n  :", selDataCnt,selData);
  // console.log(":", product[catName]);

  const [slides, setSlides] = useState([]);

  useEffect(() => {
    const newSlides = []; //   

    if(catName=="homefragrance") catName = "home";

    $.each(Array(cnt), (i) => {
      const slide = (
        <SwiperSlide key={i} className="thumb">
          <img src={process.env.PUBLIC_URL+`/images/shop/${catName}/${catName}_${seq<10?"0"+seq:seq}/thumb_${i+1<10?"0"+(i+1):(i+1)}.jpg`} />
        </SwiperSlide>
      );
      newSlides.push(slide); //    
    });

    
    setSlides(newSlides); //   
}, [selDataCnt]); // selDataCnt    useEffect 
// console.log("  ",slides)

  ///////////////////////////////////////////////////

  const [thumbsSwiper, setThumbsSwiper] = useState(null);

  /////// ///////////////////////////////
  return (
    <>
      <Swiper
        style={{
          "--swiper-navigation-color": "#1E1E1E",
          "--swiper-pagination-color": "#fff",
        }}
        spaceBetween={0}
        navigation={true}
        thumbs={{ swiper: thumbsSwiper }}
        modules={[FreeMode, Navigation,Thumbs ]}
        className="mySwiper2"
      >
       {slides.map((slide) => slide)}
      </Swiper>
      <Swiper
        onSwiper={setThumbsSwiper}
        spaceBetween={10}
        slidesPerView={4}
        freeMode={true}
        watchSlidesProgress={true}
        modules={[FreeMode, Navigation, Thumbs]}
        className="mySwiper"
      >
        {slides.map((slide) => slide)}
      </Swiper>
    </>
  );
}
