//seumit Logo component - Logo.jsx
import React from "react";
import { isrc } from "../data/img_src";

///////import area/////////////////////////////////////////////////

export default function Logo({logoStyle}) {
//logoStyle: ,  
//  : top / bottom

    //  
    const logoSetStyle = {
        top:{
            position: "absolute",
            left: "50%",
            top:" 50%",
            translate:" -50% -50%",
        },
        bottom:{
            height: "80px",
        },
    } 

    //   
    const logoImgStyle = {
        top:{width: "120px"},
        bottom:{width: "200px"},
    }

    return(
        <h1 style={logoSetStyle[logoStyle]}>
            <img
            style={logoImgStyle[logoStyle]}
            src={process.env.PUBLIC_URL+isrc.logo} alt=" " />
        </h1>
    );


}