import React, { useRef } from "react";
import $ from "jquery";

//////////data
import { mainVisualData } from "../data/main_visual_data";

//////////swiper
// Import Swiper React components
import { Swiper, SwiperSlide } from "swiper/react";
// Import Swiper styles
import "swiper/css";
import "./css/swipver_MainVisual.scss";
import { Pagination, Autoplay } from "swiper/modules";
//////////////////////import area/////////////////////////////

export default function SwipverMainVisual() {
    //  left  0  .desc-wrap  + 
    const descAni = (swiper) => {
        const activeIndex = swiper.activeIndex; // activeIndex 
        const $activeSlide = $(swiper.slides[activeIndex]);
        const $descWrap = $activeSlide.find(".desc-wrap");

        // animate fiter  .
        //   step (<   ani )    
        //   fx.prop(animate)  opacity  if   

        $descWrap
            .eq(0)
            .css({
                bottom: "70%",
                opacity: "0",
            })
            .animate(
                {
                    bottom: "20%",
                    opacity: "1",
                },
                {
                    duration: 3000,
                    step: function (now, fx) {
                        // step   
                        if (fx.prop === "opacity") {
                            $(this).css("filter", `blur(${50 * (1 - now)}px)`); //   
                        }
                    },
                }
            )
            .delay(2000)
            .fadeTo(1000, 0);
    };

    //////
    return (
        <>
            <Swiper
                loop={true}
                loopedslides={mainVisualData.length} //    
                slidesPerView={1}
                spaceBetween={0}
                pagination={{ clickable: true }}
                autoplay={{ delay: 5500, disableOnInteraction: false }}
                onInit={(swiper) => {
                    descAni(swiper);
                }}
                onSlideChangeTransitionEnd={(swiper) => {
                    descAni(swiper);
                }}
                speed={20e2}
                modules={[Pagination, Autoplay]} // Virtual  
                className="mySwiper main-visual-inner"
            >
                {mainVisualData.map((v, i) => (
                    <SwiperSlide
                        key={i}
                        className={`"item main-box bg${
                            i + 1
                        } cbx bgi swiper-slide"`}
                    >
                        <div className="desc-wrap">
                            <div className="topic">{v.topic}</div>
                            <div className="title">{v.title}</div>
                            <div className="text">{v.text}</div>
                        </div>
                    </SwiperSlide>
                ))}
            </Swiper>
        </>
    );
}
