//    ///
import React, { Fragment, useContext, useRef, useState } from "react";
import $ from "jquery";

// css
import "../../css/notice.scss";

// 
import SubTitle from "../modules/SubTitle";

//  
import { initData } from "../func/memFn";

//  JS
import { initBoardData } from "../func/board_fn";
import { sCon } from "./sCon";

///////import area////////////////////////////

export default function Notice({ selCat }) {
  //selCat :  

  // 
  const myCon = useContext(sCon);
  //     (!)
  const sts = myCon.loginSts;
  //    
  initBoardData();

  //     : 
  const baseData = JSON.parse(localStorage.getItem("board-data"));

  //   : 
  baseData.sort((a, b) =>
    Number(a.idx) > Number(b.idx) ? -1 : Number(a.idx) < Number(b.idx) ? 1 : 0
  );

  //[]///////////////////////////
  //1.  
  const [pageNum, setPageNum] = useState(1);
  //2. 
  const [mode, setMode] = useState("L");
  /* 
  [ ]
    L : ()
    R :  
    W :  
    M : ( )
  */

  //[]///////////////////
  //1.   -   , 
  const totalCount = useRef(baseData.length);
  //2.   :      (?  =>   ,   )
  const selRecord = useRef(null);

  //    
  const unitSize = 8;

  /**************************************************** 
    : bindList
    :    
 ****************************************************/

  const bindList = () => {
    //1.    (.. !!!!)
    let orgData = baseData;

    //2.   : 
    orgData.sort((a, b) =>
      Number(a.idx) > Number(b.idx) ? -1 : Number(a.idx) < Number(b.idx) ? 1 : 0
    );

    //3.    (ex:   10 ? 0~9  !)

    // (0    - 1 )
    let startNum = (pageNum - 1) * unitSize;
    // = *
    let endNum = pageNum * unitSize;

    //
    const selData = [];

    // for  
    for (let i = startNum; i < endNum; i++) {
      // console.log(i);
      //     !
      if (i >= totalCount.current) break;
      //  
      selData.push(orgData[i]);
    } ///// for //////

    return selData.map((v, i) => (
      <tr key={i}>
        {/*      */}
        <td>{i + 1 + startNum}</td>
        <td>
          <a
            href="#"
            onClick={(e) => {
              e.preventDefault();
              //  !
              setMode("R");
              //   
              selRecord.current = v;
            }}
          >
            {v.tit}
          </a>
        </td>
        <td>{v.unm}</td>
        <td>{v.date}</td>
        <td>{v.cnt}</td>
      </tr>
    ));
  }; ///////bindList Fn///////////////////////////////////

  /***************************************** 
   : pagingList
  :     
  *****************************************/
  const pagingList = () => {
    //  :  /  
    //:    ->  +1

    //1.  
    let pagingCount = Math.floor(totalCount.current / unitSize);

    //    !
    // 0  1
    if (totalCount.current % unitSize > 0) {
      pagingCount++;
    }
    // 
    const pgCode = [];

    //1     
    for (let i = 1; i <= pagingCount; i++) {
      pgCode.push(
        <Fragment key={i}>
          {
            //   ? b 
            i === pageNum ? (
              <b>{i}</b>
            ) : (
              //  
              <a
                href="#"
                onClick={(e) => {
                  e.preventDefault();
                  setPageNum(i);
                }}
              >
                {i}
              </a>
            )
          }
        </Fragment>
      );
    } ///for//////

    //
    return pgCode;
  }; ///pagingList Fn //////////////////////////

  //    ////////
  const clickButton = (e) => {
    //  
    let btnText = e.target.innerText;
    // console.log(btnText);
    //  
    switch (btnText) {
      //   
      case "Write":
        setMode("W");
        break;
      //  
      case "List":
        setMode("L");
        break;
      //   !
      case "Submit":
        submitFn();
        break;
      //    
      case "Modify":
        setMode("M");
        break;
      //    
      case "Delete":
        deleteFn();
        break;
    }
  }; ////////// clickButton //////////

  // //////////////////////
  const deleteFn = () => {
    //
    if (window.confirm("  ?")) {
      //1.   idx 
      let currIdx = selRecord.current.idx;
      //2. some()   
      baseData.some((v, i) => {
        if (v.idx == currIdx) {
          //  idx    idx ?
          // : splice(,   )
          baseData.splice(i, 1);

          //true   
          return true;
        } //if////////////
      }); //////////some////////
      /************************************** 
    find()  some() :
    find():   " "  
            > :   or undefined,     
    some() :      " "
            >  :  ? true /  false
                        
    **************************************/

      //3.   
      localStorage.setItem("board-data", JSON.stringify(baseData));

      //4.          !
      //    
      totalCount.current = baseData.length;

      //4.   ->   ! "L"
      setMode("L");
    } ///////if//////
  }; //////deleFn

  //  /////////////
  const submitFn = () => {
    //
    let title = $();
  }; /////submitFn ///////////////////

  ////   //////////////
  return (
    <>
      <SubTitle selCat="NOTICE" />
      <main id="notice-area">
        {
          //1.     
          mode == "L" && (
            <ListMode bindList={bindList} pagingList={pagingList} />
          )
        }
        {
          //2.     
          mode == "R" && <ReadMode selRecord={selRecord} sts = {sts} />
        }
        {
          //3.      
          mode == "W" && <WriteMode sts={JSON.parse(sts)} />
        }
        {
          //4.     
          mode == "M" && <ModifyMode selRecord={selRecord} />
        }
        <br />
        {/*    */}
        <table className="btn-wrap">
          <tr>
            <td>
              {
                // 1.  : : , : L
                mode == "L" && sts && (
                  <button 
                  className="button-nomal"
                  onClick={clickButton}>Write</button>
                )
              }
              {
                // 2.  : : x, : R
                <>
                  {mode == "R" && <button onClick={clickButton}>List</button>}

                  {
                    // ,   =   
                    // selRecord 
                    // uid:  
                    //sts.uid      

                    mode == "R" &&
                      sts &&
                      JSON.parse(sts).uid === selRecord.current.uid && (
                        <button onClick={clickButton}>Modify</button>
                      )
                  }
                </>
                // 
              }

              {
                // 3. (w) 
                mode == "W" && (
                  <>
                    <button onClick={clickButton}>List</button>
                    <button onClick={clickButton}>Submit</button>
                  </>
                )
              }
              {
                //4.  "M" 
                mode == "M" && (
                  <>
                    <button onClick={clickButton}>Delete</button>
                    <button onClick={clickButton}>List</button>
                    <button onClick={clickButton}>Submit</button>
                  </>
                )
              }
            </td>
          </tr>
        </table>
      </main>
    </>
  );
} /////////// Notice /////////////////////

/****************************************** 
           
******************************************/
const ListMode = ({ bindList, pagingList }) => {
  return (
    <>

      <table className="dtbl" id="board">
        <thead>
          <tr>
            <th>Number</th>
            <th>Title</th>
            <th>Writer</th>
            <th>Date</th>
            <th>Hits</th>
          </tr>
        </thead>
        <tbody>{bindList()}</tbody>
        <tfoot>
          <tr>
            <td colSpan="5" className="paging">
              {pagingList()}
            </td>
          </tr>
        </tfoot>
      </table>
      <div className="selbx">
        <select name="cta" id="cta" className="cta">
          <option value="tit">Title</option>
          <option value="cont">Contents</option>
          <option value="unm">Writer</option>
        </select>
        <select name="sel" id="sel" className="sel">
          <option value="0">Descending</option>
          <option value="1">Ascending</option>
        </select>
        <input id="stxt" type="text" maxLength="50" />
        <button className="sbtn button-nomal">Search</button>
      </div>
    </>
  );
}; //////////// ListMode ///////////////////

/*********************************************** 
               
***********************************************/

const ModifyMode = ({ selRecord, sts }) => {

  //  ? =   !
  // ->   !
  //    
  const data = selRecord.current;

  return (
    <>
      <table className="dtblview readone">
        <caption>option : Modify</caption>
        <Fragment>
        <tbody>
          <tr>
            <th>Name</th>
            <td>
              <input
                type="text"
                className="name"
                size="20"
                readOnly
                value={data.unm}
              />
            </td>
          </tr>
          <tr>
            <th>Title</th>
            <td>
              <input
                type="text"
                className="subject"
                size="60"
                readOnly
                value={data.tit}
              />
            </td>
          </tr>
          <tr>
            <th>Content</th>
            <td>
              <textarea
                className="content"
                cols="60"
                rows="10"
                readOnly
                value={data.cont}
              ></textarea>
            </td>
          </tr>
          <tr>
            <th>Attachment</th>
            <td></td>
          </tr>
        </tbody>
        </Fragment>
      </table>
    </>
  );
}; //////////////ModifyMode

/*********************************************** 
               
***********************************************/

const WriteMode = ({ sts }) => {
  //sts -  
  //   !
  return (
    <>
      <table className="dtblview readone">
        <caption>OPINION : Write</caption>
        <Fragment>
        
        <tbody>
          <tr>
            <th>Name</th>
            <td>
              <input
                type="text"
                className="name"
                size="20"
                readOnly
                //  
                value={sts.unm}
              />
            </td>
          </tr>
          <tr>
            <th>Email</th>
            <td>
              <input
                type="text"
                className="email"
                size="40"
                readOnly
                //  
                value={sts.eml}
              />
            </td>
          </tr>
          <tr>
            <th>Title</th>
            <td>
              <input type="text" className="subject" size="60" />
            </td>
          </tr>
          <tr>
            <th>Content</th>
            <td>
              <textarea className="content" cols="60" rows="10"></textarea>
            </td>
          </tr>
          <tr>
            <th>Attachment</th>
            <td></td>
          </tr>
        </tbody>
        </Fragment>
      </table>
    </>
  );
}; //////////////WriteMode

/*********************************************** 
               
***********************************************/

