import { TopArea } from "./TopArea";
import MainArea from "./MainArea";
import FooterArea from "./FooterArea";
import { useCallback, useEffect, useRef, useState } from "react";
import { sCon } from "../pages/sCon";
import { useNavigate } from "react-router-dom";
import { RecoilRoot } from "recoil";
import CartList from "../modules/CartList";
///////import area///////////////////////////////////

export default function Layout() {
  let loginTemp = null;
  if (sessionStorage.getItem("login-sts"))
    loginTemp = sessionStorage.getItem("login-sts");

  ///[]///////////////////////////////////////////
  //1.   
  const [introSts, setIntroSts] = useState(true);
  const [loginSts, setLoginSts] = useState(
    sessionStorage.getItem("login-sts") || "{}" //      
  );
  const [loginMsg, setLoginMsg] = useState(
    sessionStorage.getItem("login-msg") || "" //      
  );
  // console.log("?", loginSts);
  //  
  const [recentSearches, setRecentSearches] = useState([]);

  //  
  const [localSearch, setLocalSearch] = useState(
    localStorage.getItem("searchLog")
  );

  //////////cart/////////////////////////////////////////////
  //   
  let cartTemp = false;

  // [     ] ///
  const [localsCart, setLocalsCart] = useState(
    JSON.parse(localStorage.getItem("cart-data") || "[]") //      
  );
  //  : true 
  const [cartSts, setCartSts] = useState(false);

  //   
  const[selectedOpt, setSelectedOpt] = useState([]);

  //       
  if (localsCart) {
    //   cartTemp true 
    //   0 !
    let localsCart = localStorage.getItem("cart-data") || "[]"; //      
    let cartCnt = JSON.parse(localsCart).length;
    // console.log(" :", cartCnt);
    if (cartCnt > 0) cartTemp = true;
  } ////////////  if ////////

 
  const searchLog = useRef(
    JSON.parse(localStorage.getItem("search-log") || "[]")
  );

  ////[ ]///////////////////////////////////////
  //1.  :   useNavigate  useCallback 
  const goNave = useNavigate();
  const goPage = useCallback((pm1, pm2) => {
    goNave(pm1, pm2);
  }, []);

  //2.   
  const makeMsg = useCallback((name) => {
    setLoginMsg(`${name},   `);
  }, []); ////makeMsg///////////////////

  //3.  
  const logoutFn = useCallback(() => {
    //1.   null
    setLoginSts(null);
    //2.   : minfo
    sessionStorage.removeItem("login-sts");
    //3.   
    // setLoginMsg(null);
    //4.  
    goPage("/");
  }, []);

  //selCat =  
  const [selCat, setSelCat] = useState("face");

  //  /////////////////////////////////////////////////////

  useEffect(() => {
    try {
      const minfo = JSON.parse(sessionStorage.getItem("minfo")) || {};
      setLoginSts(minfo);
      makeMsg(minfo.unm);
    } catch (error) {
      console.error("sessionStorage   :", error);
      setLoginSts(null); //    
    }
  }, []);


  // useEffect(() => {
  //   //  //////////////
  //   //  minfo  null    
  //   //> null   true 
  //   if (sessionStorage.getItem("minfo")) {
  //     // 
  //     let sessionSts = sessionStorage.getItem("minfo");
  //     // 
  //     setLoginSts(sessionSts);
  //     //  
  //     //>  unm() 
  //     makeMsg(JSON.parse(sessionSts).unm);
  //   } ////if/////////////
  // }, []);

  //  ///////////////
  return (
    <sCon.Provider
      value={{
        setIntroSts,
        selCat,
        setSelCat,
        setLoginSts,
        loginMsg,
        setLoginMsg,
        goPage,
        makeMsg,
        logoutFn,
        recentSearches,
        setRecentSearches,
        searchLog,
        setCartSts,
        setLocalsCart,
        localsCart,
        selectedOpt,
        setSelectedOpt,
      }}
    >
      <TopArea
        loginMsg={loginMsg}
        loginSts={loginSts}
        logoutFn={logoutFn}
        goPage={goPage}
        setRecentSearches={setRecentSearches}
        recentSearches={recentSearches}
        localSearch={localSearch}
        setLocalSearch={setLocalSearch}
        
      />
      {/*  :  true  */}
      {cartSts && <CartList />}
      <MainArea />
      <FooterArea 
      ival={introSts} 
      />
    </sCon.Provider>
  ); /////return
} //////layout
